import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
declare var X2JS: any;

@Injectable()
export class CommonserviceService {
  quesXml: "./data/sod_t4_v1_u1_w1___ph_act1__.xml";
  parsedData;

  constructor(private _http: Http) { }

/*  ok button status update starts*/
  private enableOkBtnStatus = new BehaviorSubject<number>(0);
  enableOkBtn = this.enableOkBtnStatus.asObservable();

  // change previous next button status
  okBtnStatus(status) {
    this.enableOkBtnStatus.next(status);
  }

/* ok button status update ends*/

/* previous next button click update starts*/
  private prevNxtStatus = new BehaviorSubject<string>('');
  gotoPrevNext = this.prevNxtStatus.asObservable();

  // change previous next button status
  chngPrevNxtStatus(status) {
    this.prevNxtStatus.next(status);
  }

/* previous next button click update ends*/


/* modal status update starts */
  private modalStatus = new BehaviorSubject<string>('none');
  cast = this.modalStatus.asObservable();

   // help screen modal status update
  editStatus(status) {
    this.modalStatus.next(status);
  }
/* modal status update ends */

/* next previous button enable disable status update starts */
  private nxtPrevEnblDisbl = new BehaviorSubject<Object>({});
  nxtPrevEnblDisblStatus = this.nxtPrevEnblDisbl.asObservable();

   // help screen modal status update
  chngPrevNxtEnblDisbl(status) {
    this.nxtPrevEnblDisbl.next(status);
  }
/* next previous button enable disable status update ends */

/* make http service call */
  getXMLDataFromFactory(url): Observable<any> {
    const curLocation = location.origin;
    return this._http.get(`./assets/iteractives/sorting_open_defined/data/${url}`)
      .map((response: Response) => {
        let x2js = new X2JS();
        let aftCnv = x2js.xml_str2json(response["_body"]);
        return aftCnv;
      })
      .catch(this.handleError);
  }

  handleError(error: Response) {
    console.error(error);
    return Observable.throw(error);
  }

  setData() {
  }

  getData() {
    return this.parsedData.sorting_open_defined;
  }
}



