import { Component, OnInit, Input } from '@angular/core';
import { CommonserviceService } from '../../common/services/commonservice.service';

@Component({
  selector: 'app-instruction',
  templateUrl: './instruction.component.html',
  styleUrls: ['./instruction.component.less']
})
export class InstructionComponent implements OnInit {

  constructor(private CommonserviceService: CommonserviceService) {
  }
  // hold the value of instruction text
  @Input()
  instructionInfo: Object;
  // instructionHeader;
  instructionTxt;
  audioSrc: any;
  modalDisplayStatus: String = 'block';

  ngOnInit() {
    this.CommonserviceService.cast.subscribe(modalStatus => this.modalDisplayStatus = modalStatus);
    // this.instructionHeader = this.instructionInfo["instructionHeader"];
    this.instructionTxt = this.instructionInfo['instructionTxt'];
    // for audio
    this.audioSrc = this.instructionInfo['audioSrc'];
  }

  closeModal() {
    this.modalDisplayStatus = 'none';
  }
  playAudio(){
    let audio = new Audio();
    audio.src = this.audioSrc;
    audio.load();
    audio.play();
    }

}



