import { Component, OnInit, Input, Output } from '@angular/core';
import { CommonserviceService } from '../../common/services/commonservice.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.less']
})
export class HeaderComponent implements OnInit {

  constructor(private CommonserviceService: CommonserviceService) { }
  headerTitle: String = "Dummy";
  modalDisplayStatus: String = 'none';

  @Input()
  headerInfo:Object;

  ngOnInit() {
     this.CommonserviceService.cast.subscribe(modalStatus => this.modalDisplayStatus = modalStatus);
     this.headerTitle = this.headerInfo['headingTitle'];
  }

  /* functionality for help icon*/
  help(){
    //  open help video modal
    this.modalDisplayStatus = 'block';
    this.CommonserviceService.editStatus(this.modalDisplayStatus);
  }
  
  /* functionaly for exit icon*/
  closeWindow(){
    window.close();
  }

}



