import { Injectable } from '@angular/core';

@Injectable()
export class UserResponseService {

  userResponse: Object = {
    isAnswered: false,
    noOfAttempts: 0,
    Qs_ans_resp: []
  };
  constructor() { }
  init(obj, templateName) {
    let initData = {
      question: '',
      correct: false,
      userAnswer: '',
      correctAnswer: ''
    };
    switch (templateName) {

      case 'drag-drop': {
        for (let i in obj) {
          obj[i].screens.item.userResponse = {
            isAnswered: false,
            noOfAttempts: 0,
            Qs_ans_resp:[]
          };
          for(let j=0; j< obj[i].screens.item.sentence.p.length ;j++ ){
            obj[i].screens.item.userResponse.Qs_ans_resp.push(initData);
          }
        }
        break;
      }
    }
  }
}



