import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { sortingOpenDefinedComponent } from './interactives/sorting_open_defined/sod.component';
import { HeaderComponent } from './components/header/header.component';
import { NavigationComponent } from './components/navigation/navigation.component';
import { InstructionComponent } from './components/instruction/instruction.component';
import { AppComponent } from './app.component';
import { ErrorPageComponent } from './components/error-page/error-page.component';
import { SodHomeComponent } from './interactives/sod-home/sod-home.component';
import {DragDropComponent} from './interactives/drag-drop/drag-drop.component';
const appRoutes: Routes = [
  { path: '', redirectTo: '/drag-drop', pathMatch: 'full',component: DragDropComponent },
  {
    path: 'sorting-open-defined/:url', component: SodHomeComponent,
    children: [
      { path: ':id', component: SodHomeComponent }
    ]
  }, {
    path: 'drag-drop/:url', component: DragDropComponent,
    children: [
      { path: ':id', component: DragDropComponent }
    ]
  },
  {
    path: 'click-to-choose',
    // canActivate: [],
    canActivateChild: [],
    component: NavigationComponent
  },
  // { path: 'not-found', component: PageNotFoundComponent },
  { path: 'not-found', component: ErrorPageComponent, data: { message: 'Page not found!' } },
  { path: '**', redirectTo: '/not-found' }
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes,{useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }



