import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { ErrorPageComponent } from './components/error-page/error-page.component';
import { AppRoutingModule } from './app-routing.module';

/* common components */
import { AppComponent } from './app.component';
import { HeaderComponent } from './components/header/header.component';
import { NavigationComponent } from './components/navigation/navigation.component';
import { InstructionComponent } from './components/instruction/instruction.component';
import { ResultScreenComponent } from './components/endscreen/result-screen.component';

/* services */
import { CommonserviceService } from './common/services/commonservice.service';

/* template components */
import { sortingOpenDefinedComponent } from './interactives/sorting_open_defined/sod.component';
import { SodHomeComponent } from './interactives/sod-home/sod-home.component';
import { DragDropComponent } from './interactives/drag-drop/drag-drop.component';
import { AssignmentComponent } from './interactives/drag-drop/assignment/assignment.component';
import { QuestionComponent } from './interactives/drag-drop/assignment/question/question.component';
import { OptionComponent } from './interactives/drag-drop/assignment/option/option.component';
import {DragdropService} from './common/services/dragdropservice.service';
import { ValidatorComponent } from './interactives/drag-drop/assignment/validator/validator.component';
import {UserResponseService} from './common/services/user-response.service';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    NavigationComponent,
    InstructionComponent,
    sortingOpenDefinedComponent,
    SodHomeComponent,
    ErrorPageComponent,
    DragDropComponent,
    AssignmentComponent,
    QuestionComponent,
    OptionComponent,
    ValidatorComponent,
    ResultScreenComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpModule
  ],
  providers: [CommonserviceService,DragdropService, UserResponseService],
  bootstrap: [AppComponent]
})
export class AppModule { }



